.App {
  text-align: center;
}

div {
  box-sizing: border-box;
}
.list {
  padding:20px;
  text-align: left;
  border-bottom: 1px solid grey;
}
.modal {
  margin-top: 20px;
  padding: 20px;
  background: #eee;
  text-align: left;
}
.write {
  margin-top: 30px;
  background-color: #c2fb61;
  border: 0;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
}
.write:hover {
  background-color: #afdb64;
  transition: .3s;
}
#writeTxt {
  padding: 10px 20px;
  margin-right: 10px;
}

.black-nav {
  display: flex;
  background-color: #000;
  width: 100%;
  color: white;
  padding-left: 20px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
